@import url("https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;600&display=swap");

.main {
  width: 100%;
  height: 60%;
  background: #1a1a1a;
  color: #fff;
  display: flex;
  justify-content: space-evenly;
}
.location {
  margin-top: 50px;
}
.location p {
  color: #c7cfc7;
  width: 20px;
  margin-top: 30px;
  height: auto;
}
.location h2 {
  font-family: "Poppins", sans-serif;
  font-size: 16px;
  font-weight: 400;
}
.cont {
  margin-top: 50px;
}

.cont p {
  color: #c7cfc7;
  margin-top: 30px;
  margin-bottom: 15px;
  height: auto;
}
.cont h2 {
  font-family: "Poppins", sans-serif;
  font-size: 16px;
  font-weight: 400;
}
.cont span {
  color: #c7cfc7;
}
.business {
  margin-top: 50px;
}
.business p {
  color: #c7cfc7;
  width: 200px;
  margin-top: 30px;
  height: auto;
}
.business span {
  display: block;
  color: #c7cfc7;
}
.business h2 {
  font-family: "Poppins", sans-serif;
  font-size: 16px;
  font-weight: 400;
}
.follow {
  margin-top: 50px;
}
.follow h2 {
  font-family: "Poppins", sans-serif;
  font-size: 16px;
  font-weight: 400;
}
.follow .icon {
  margin-top: 30px;
  margin-bottom: 15px;
  height: auto;
  margin-left: 10px;
}
.follow .fb {
  color: #fff;
}
.follow .yt {
  color: red;
}
.copy {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-end;
  text-align: center;
  color: #c7cfc7;
  padding: 20px;
  font-size: 16px;
  background: #1a1a1a;
}
