@import url("https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;600&display=swap");
* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  color: #fff;
  text-decoration: none;
}
.video {
  position: absolute;
  width: 100%;
  height: 100vh;
  top: 50%;
  left: 50%;
  object-fit: cover;
  transform: translate(-50%, -50%);
  z-index: -1;
  filter: brightness(50%);
}
.head {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: space-evenly;
}
.logo {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.logo img {
  width: 50px;
  cursor: pointer;
}
.logo h1 {
  font-size: 20px;
}
.logo .right-o {
  color: lightgreen;
}
.logo .left-o {
  color: skyblue;
}
.navbar ul {
  display: flex;
  padding: 35px;
}
.navbar li {
  font-family: "Poppins", sans-serif;
  font-weight: 500;
  list-style: none;
  margin-right: 50px;
  cursor: pointer;
}
