@import url("https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;600&display=swap");

.cards {
  position: relative;
  width: 100%;
  height: 150%;
  background: #000;
  border: none;
}
.cm {
  display: flex;
  justify-content: center;
  align-items: center;
  transition: ease-in-out 0.5s;
}
.cm:hover {
  transform: scale(1.1);
  background: #fff;
}
.cm .after {
  display: none;
}
.cm:hover .before {
  display: none;
}
.cm:hover .after {
  display: block;
  align-items: center;
  color: #000;
  font-family: "Poppins", sans-serif;
}
.cm:hover .txt {
  color: gray;
  display: flex;
  flex-direction: column;
  font-size: 10px;
}
