@import url("https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;600&display=swap");

.contact-us {
  width: 100%;
  height: 100vh;
  margin-top: 120px;
  font-family: "Poppins", sans-serif;
}
.hr-div {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-top: 20px;
}
.hr-div hr.hr {
  width: 20px;
  border: none;
  height: 1px;
  margin: 3.3px;
  background: red;
}
.hr-div hr.hr2 {
  width: 35px;
  border: none;
  height: 1px;
  background: red;
}
.text p {
  color: #000;
}
.text h1 {
  color: #000;
  margin-top: 20px;
}
.text .touch {
  color: red;
  font-family: "Poppins", sans-serif;
  font-weight: 600;
}
.text .desciption {
  width: 40%;
  height: auto;
  font-family: "Poppins", sans-serif;
  color: gray;
  margin-left: 30%;
  margin-bottom: 50px;
  margin-top: 50px;
}
.form {
  width: 50%;
  margin: auto;
  display: flex;
  flex-direction: column;
}
.text-field {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.text-field .input {
  width: 49%;
  height: 40px;
  color: #000;
  padding: 10px;
}
.subject {
  width: 100%;
  margin-top: 20px;
  height: 40px;
  padding: 10px;
  color: #000;
}

.msg {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 150px;
  margin-top: 20px;
  padding: 10px;
  color: #000;
}
.term {
  display: flex;
  width: 100%;
  align-items: center;
}
.check-box {
  align-items: center;
  margin: 10px;
}
.term p {
  color: #000;
}
.term span {
  color: crimson;
}
.btn-div {
  width: 100%;
  display: flex;
  align-items: center;
}
#btn {
  width: 100%;
  background: crimson;
  outline: none;
  border: none;
  padding: 10px;
}
#btn:hover {
  background: rgb(211, 29, 16);
}
