* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  color: #fff;
  text-decoration: none;
}
.video {
  position: absolute;
  width: 100%;
  height: 100vh;
  top: 50%;
  left: 50%;
  object-fit: cover;
  transform: translate(-50%, -50%);
  z-index: -1;
  filter: brightness(50%);
}
.head {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: space-evenly;
}
.logo {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.logo img {
  width: 50px;
  cursor: pointer;
}
.logo h1 {
  font-size: 20px;
}
.logo .right-o {
  color: lightgreen;
}
.logo .left-o {
  color: skyblue;
}
.navbar ul {
  display: flex;
  padding: 35px;
}
.navbar li {
  font-family: "Poppins", sans-serif;
  font-weight: 500;
  list-style: none;
  margin-right: 50px;
  cursor: pointer;
}

.heading {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  width: 100%;
  height: 80vh;
  text-align: center;
  position: relative;
}
.heading h1 {
  font-size: 80px;
  margin: 20px 0;
}
.heading h3 {
  font-size: 30px;
}
.btn {
  display: flex;
  flex-direction: row;
  margin-top: 40px;
}
.btn button {
  width: 150px;
  height: 50px;
  color: #000;
  margin-left: 50px;
  border-radius: 30px;
  font-size: 16px;
  font-weight: 600;
  border: none;
  outline: none;
  cursor: pointer;
}
.btn .read-more {
  background: rgb(224, 42, 79);
  color: #fff;
}
.btn .read-more:hover {
  background: rgb(179, 7, 41);
}
.btn .contact {
  background: #fff;
  color: #000;
}
.btn .contact:hover {
  background: transparent;
  border: 1px solid #fff;
  color: #fff;
}

