.heading {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  width: 100%;
  height: 80vh;
  text-align: center;
  position: relative;
}
.heading h1 {
  font-size: 80px;
  margin: 20px 0;
}
.heading h3 {
  font-size: 30px;
}
.btn {
  display: flex;
  flex-direction: row;
  margin-top: 40px;
}
.btn button {
  width: 150px;
  height: 50px;
  color: #000;
  margin-left: 50px;
  border-radius: 30px;
  font-size: 16px;
  font-weight: 600;
  border: none;
  outline: none;
  cursor: pointer;
}
.btn .read-more {
  background: rgb(224, 42, 79);
  color: #fff;
}
.btn .read-more:hover {
  background: rgb(179, 7, 41);
}
.btn .contact {
  background: #fff;
  color: #000;
}
.btn .contact:hover {
  background: transparent;
  border: 1px solid #fff;
  color: #fff;
}

/* Silder code */
.ads {
  width: 100%;
  background: #fff;
  height: 200px;
}
marquee {
  margin-top: 70px;
}
.ads img {
  width: 300px;
  margin-left: 20px;
}
