@import url("https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;600&display=swap");

.design {
  width: 100%;
  height: 100vh;
  color: #fff;
  background: #000;
  text-align: center;
}
.hr-div {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-top: 20px;
}
.hr-div hr.hr {
  width: 20px;
  border: none;
  height: 1px;
  margin: 3.3px;
  background: red;
}
.hr-div hr.hr2 {
  width: 35px;
  border: none;
  height: 1px;
  background: red;
}
.heading-design {
  font-style: "Poppins";
  display: block;
  margin-top: 30px;
}
.heading-design p {
  font-size: 20px;
  margin-bottom: 10px;
  padding-top: 40px;
}
.heading-design h1 {
  font-size: 40px;
}
.heading-design .description {
  width: 55%;
  height: auto;
  margin-top: 50px;
  margin-left: 21%;
  text-align: center;
}
.title {
  display: flex;
  justify-content: center;
  margin-top: 150px;
}
.title a {
  margin-left: 20px;
}
